:root {
  --classic-black: #000000;
  --text-classic:#1F1E25;
  --text-opacity:#707C87;
  --text-button:#F6F7FF;
  --text-opasity-2: #E0E1EA;
  --button-color: #2C36F2;
}

main {
  display: flex;
  flex-direction: column;
}

.page__body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  height: 100%;
  padding: 0;
  margin: 0;
}

.page__main {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: var(--text-button);
}

.header-case {
  width: 1170px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding-top: 25px;
  padding-bottom: 34px;
}

.header-logo__tagline {
  width: 150px;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  width: 684px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.header__nav-item {
  margin-right: 24px;
  font-size: 16px;
  line-height: 19px;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-item a,
.header__login-link {
  text-decoration: none;
  color: #1F1E25;
}

.header__nav-item a:hover {
  color: #2C36F2;
}

.header__login-link {
  display: flex;
  justify-content: flex-end;
  position: relative;
  width: 210px;
  margin-left: 10px;
}

.header__login-link::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 22px;
  top: -3px;
  left: 6px;
  background-image: url("../../image/door.svg");
}

.banner {
  min-height: 400px;
  background-color: var(--button-color);
  margin-bottom: 61px;
  background-image: url("../../image/main-image.png");
}

.banner__gradient {
  min-height: 400px;
  background: linear-gradient(180.24deg, rgba(32, 39, 179, 0) 36.31%, #2027B3 99.79%);
}

.banner__wrapper {
  position: relative;
  max-width: 1066px;
  margin: 0 auto;
  min-height: 400px;
  background-image: url("../../image/main-image.png");
  background-repeat: no-repeat;
}

.banner__wrapper::before {
  content: "";
  position: absolute;
  width: 444px;
  height: 286px;
  top: 62px;
  right: 74px;
  background-image: url("../../image/card-bank.png");
  background-repeat: no-repeat;
}

.banner__description {
  width: 450px;
  padding-top: 103px;
  margin-left: 118px;
}

.banner__header {
  margin: 0;
  padding: 0;
  font-size: 54px;
  line-height: 63px;
  font-weight: 700;
  color: var(--text-button);
}

.banner__text {
  margin: 0;
  padding: 0;
  font-size: 22px;
  line-height: 26px;
  font-weight: 400;
  color: var(--text-opasity-2);
  margin-top: 8px;
}

.banner__button {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  background-color: var(--text-button);
  padding: 15px 36px 17px 36px;
  border-radius: 4px;
  margin-top: 34px;
  margin-bottom: 34px;
}

.banner__button-text {
  color: var(--text-classic);
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto"), url("../../fonts/Roboto-Medium.woff2") format("woff2"), url("../../fonts/Roboto-Medium.ttf") format("ttf");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto"), url("../../fonts/Roboto-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../../fonts/Roboto-Regular.woff2") format("woff2");
}
.footer-description {
  padding: 49px 0;
  background-color: var(--text-button);
}

.footer-description__wrapper {
  display: flex;
  width: 1170px;
  margin: 0 auto;
}

.adress {
  width: 253px;
  margin-right: 80px;
}

.adress__text {
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  color: var(--text-opacity);
}

.services {
  width: 222px;
  margin-right: 31px;
}

.services__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 6px;
}

.services__item {
  font-weight: 500;
  line-height: 23px;
  margin-bottom: 8px;
}

.services__item a {
  text-decoration: none;
  color: var(--text-classic);
}

.mobile {
  width: 168px;
  margin-top: 9px;
  margin-right: 112px;
}

.mobile__code {
  position: relative;
  line-height: 23px;
}

.mobile__code::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 16px;
  top: 1px;
  left: -29px;
  background-image: url("../../image/phone.svg");
  background-repeat: no-repeat;
}

.mobile__description {
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  color: var(--text-opacity);
  margin-top: 1px;
}

.contacts {
  width: 123px;
  margin-top: 9px;
  margin-right: 39px;
}

.contacts__mobile {
  position: relative;
  font-size: 16px;
  line-height: 23px;
  text-decoration: none;
  color: var(--text-classic);
  font-weight: 500;
}

.contacts__mobile::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 1px;
  left: -33px;
  background-image: url("../../image/phone-contact.svg");
  background-repeat: no-repeat;
}

.contacts__description {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--text-classic);
  margin: 0;
}

.social {
  width: 142px;
}

.social__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-top: 12px;
  margin-left: 37px;
}

.social__item {
  margin-right: 16px;
}

.social__item:last-child {
  margin-right: 0;
}

.information {
  width: 1170px;
  margin: 0 auto;
  margin-bottom: 30px;
}

.information__tabs-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.information__tabs-item {
  position: relative;
  width: 292px;
  height: 93px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
}

.information__tabs-item:nth-child(1)::before {
  position: absolute;
  content: "";
  top: 30px;
  left: 91px;
  width: 34px;
  height: 33px;
  background-image: url("../../image/vault.svg");
  background-repeat: no-repeat;
}

.information__tabs-item:nth-child(2)::before {
  position: absolute;
  content: "";
  top: 31px;
  left: 86px;
  width: 34px;
  height: 30px;
  background-image: url("../../image/cards.svg");
  background-repeat: no-repeat;
}

.information__tabs-item:nth-child(3)::before {
  position: absolute;
  content: "";
  top: 29px;
  left: 73px;
  width: 28px;
  height: 34px;
  background-image: url("../../image/security.svg");
  background-repeat: no-repeat;
}

.information__tabs-item:nth-child(4)::before {
  position: absolute;
  content: "";
  top: 29px;
  left: 59px;
  width: 20px;
  height: 34px;
  background-image: url("../../image/phone-tab.svg");
  background-repeat: no-repeat;
}

.information__tabs-item--active {
  background: #F6F7FF;
  border-radius: 4px 4px 0px 0px;
}

.information__tabs-text {
  color: var(--text-classic);
  text-decoration: none;
  margin-left: 38px;
}

.information__wrapper {
  background: #F6F7FF;
  padding: 54px 135px 60px 70px;
}

.information__case {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.information__centering {
  width: 391px;
}

.information__header {
  margin: 0;
  font-weight: 500;
  font-size: 22px;
  line-height: 31px;
  margin-bottom: 26px;
}

.information__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  list-style-image: url("../../image/Vector.svg");
  margin-left: 20px;
  margin-bottom: 20px;
}

.information__item {
  font-size: 18px;
  line-height: 23px;
  margin-bottom: 15px;
}

.information__image-wrapper {
  margin-top: 6px;
}

.information__button {
  border: none;
  background-color: var(--button-color);
  width: 198px;
  height: 51px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: var(--text-button);
}